ty: 0;\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  &.disabled {\r\n    opacity: 0.5;\r\n    cursor: not-allowed;\r\n  }\r\n\r\n  .switch-pane {\r\n    display: flex;\r\n    flex-direction: column;\r\n    height: 100%;\r\n    min-height: 100%;\r\n    justify-content: flex-start;\r\n    align-items: center;\r\n    top: -100%;\r\n    position: relative;\r\n    pointer-events: none;\r\n\r\n    >span {\r\n      display: block;\r\n      min-height: 100%;\r\n    }\r\n\r\n    .switch-label {\r\n      &-checked {\r\n        opacity: 0;\r\n      }\r\n\r\n      &-unchecked {\r\n        opacity: 1;\r\n      }\r\n    }\r\n  }\r\n}\r\n","//\r\n// Datepicker\r\n//\r\n\r\nngb-datepicker {\r\n  background-color: $dropdown-bg;\r\n  position: absolute;\r\n  z-index: $zindex-dropdown;\r\n}\r\n\r\nngb-datepicker-navigation-select>.custom-select {\r\n  margin: 0 5px;\r\n}\r\n\r\n.ngb-dp-day,\r\n.ngb-dp-today {\r\n  outline: none !important;\r\n\r\n  .btn-light,\r\n  .btn-primary {\r\n    box-shadow: none !important;\r\n    outline: none !important;\r\n    // border-radius: 50%;\r\n    border: none !important;\r\n  }\r\n}\r\n\r\n.ngb-dp-weekday {\r\n  font-style: normal !important;\r\n}\r\n\r\n.ngb-tp {\r\n  box-shadow: $box-shadow;\r\n  background-color: $dropdown-bg;\r\n  margin-top: 5px;\r\n  float: left;\r\n  min-width: 10rem;\r\n  padding: 0.75rem;\r\n  margin: 0.125rem 0 0;\r\n  font-size: 0.875rem;\r\n  color: $dropdown-color;\r\n  text-align: left;\r\n  list-style: none;\r\n  background-clip: padding-box;\r\n  border: $dropdown-border-width solid $dropdown-border-color;\r\n  border-radius: $dropdown-border-radius;\r\n  position: absolute;\r\n  z-index: $zindex-dropdown;\r\n}\r\n\r\n// Date Range\r\n.custom-day {\r\n  line-height: 2rem;\r\n  border-radius: 50%;\r\n  padding: 0 !important;\r\n  outline: none !important;\r\n\r\n  &:hover {\r\n    background-color: $primary !important;\r\n  }\r\n\r\n  &.range {\r\n    background-color: $primary !important;\r\n  }\r\n\r\n  &.range